h1 {
  @extend %h1;
}

h2 {
  @extend %h1;
  font-size: var(--h2-font-size);
  line-height: 1.2;
  text-align: center;

  @include onTablet {
    text-align: left;
  }

  @include onPC {
    text-align: left;
  }
}

h3 {
  @extend %h1;
  font-size: 36px;
  text-align: center;
  margin-bottom: 40px;

  @include onTablet {
    font-size: 48px;
    margin-bottom: 48px;
    text-align: left;
  }

  @include onPC {
    font-size: 48px;
    margin-bottom: 56px;
    text-align: left;
  }
}

h4 {
  @extend %h1;
  font-size: var(--h4-font-size);
  font-weight: 400;
  text-align: left;
  text-transform: unset;
}

h5 {
  @extend %h5;
  margin: 0 0 20px;
}

p {
  @extend %p;
}

.p--menu {
  @extend %p--menu;
  transform: translateX(var(--padding-h));
  margin-bottom: 25px;

  @include onTablet {

  }

  @include onPC {
    margin-bottom: 75px;
  }


}
