.container {
  scroll-behavior: smooth;
  max-width: 1280px;
  margin: 0 auto;
  display: grid;
  grid-auto-flow: column;

  grid-template-areas:
  'header    header'
  'tickets   tickets'
  'actual    actual'
  'gallery   gallery'
  'subscribe subscribe'
  'contacts  contacts';

  gap: 0 20px;

  @include onTablet {
    grid-template-columns: repeat(6, 1fr);
    gap: 0 30px;
    grid-template-areas:
    'tickets    tickets    tickets    header     header     header'
    'actual     actual     actual     actual     actual     actual'
    'gallery    gallery    gallery    gallery    gallery    gallery'
    'subscribe  subscribe  subscribe  subscribe  subscribe  subscribe'
    'contacts   contacts   contacts   contacts   contacts   contacts';
  }

  @include onPC {
    grid-template-columns: repeat(12, 1fr);
    gap: 0 30px;
    grid-template-areas:
    'tickets    tickets    tickets    tickets    tickets    header     header     header     header     header     header     header'
    'actual     actual     actual     actual     actual     actual     actual     actual     actual     actual     actual     actual'
    'gallery    gallery    gallery    gallery    gallery    gallery    gallery    gallery    gallery    gallery    gallery    gallery'
    'subscribe  subscribe  subscribe  subscribe  subscribe  subscribe  subscribe  subscribe  subscribe  subscribe  subscribe  subscribe'
    'contacts   contacts   contacts   contacts   contacts   contacts   contacts   contacts   contacts   contacts   contacts   contacts';
  }
}
