.subscribe {
  padding: 0 var(--padding-h);
  margin-top: var(--margin-top-sections);
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-area: subscribe;

  height: 386px;
  background-image: url("../images/0.jpg");
  background-size: cover;
  background-position: center;

  @include onTablet {
    height: 380px;
  }

  @include onPC {
    height: 550px;
  }



  &__title {
    color: #fff;
    margin-bottom: 20px;
    text-transform: unset;
    text-align: center;
  }

  &__description {
    color: #fff;
    font-size: 18px;
    font-weight: 400;
    text-align: center;

    @include onTablet {
      line-height: 1;
    }

    @include onPC {
      line-height: 1;
    }
  }

  &__form {
    display: flex;
    justify-content: center;
    margin-top: 50px;
  }

  &__email {
    width: 230px;
    height: 50px;
    border: 0;
    padding: 18px 24px;
    outline: 0;
    @extend %p--email;
    color: black;

    @include onTablet {
      width: 374px;
      height: 70px;
      margin-right: 16px;
    }

    @include onPC {
      width: 470px;
      height: 70px;
      margin-right: 30px;
    }



    &::placeholder {
      @extend %p--email;
    }
  }

  &__submit-label {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    width: 50px;
    background-color: #f14a27;
    cursor: pointer;
    transition: background-color var(--dflt-trnst-time);

    &:hover {
      background-color: #f25536;
    }

    @include onTablet {
      height: 70px;
      width: 70px;
    }

    @include onPC {
      height: 70px;
      width: 70px;
    }


  }

  &__submit {
    display: none;
  }

  &__img {
    width: 7.41px;
    height: 12px;
  }
}
